import React from "react";

import { Row, Column } from "components/Grid";

import {
  ValuePointsProps,
  HelpProps,
} from "components/product/business-cash-account/types";

import Icon from "components/product/business-cash-account/Icon";
import Fade from "react-reveal-animation/Fade";
import Zoom from "react-reveal-animation/Zoom";

import arrowIcon from "assets/img/icons/gemini-orange/arrow-right.svg";

import {
  Container,
  TitleWrapper,
  Title,
  LinkContainer,
  Arrow,
  Link,
  ValuePoints,
  ValuePoint,
  ValuePointInner,
  ValuePointTitle,
  ValuePointBody,
} from "./elements";

const ValuePointsComponent: React.SFC<ValuePointsProps> = ({ valuePoints }) => (
  <ValuePoints>
    {valuePoints.map(({ iconProps, title, body }) => (
      <ValuePoint key={title}>
        <Zoom>
          <Icon {...iconProps} />
        </Zoom>
        <Fade bottom cascade>
          <ValuePointInner>
            <ValuePointTitle>{title}</ValuePointTitle>
            <ValuePointBody>{body}</ValuePointBody>
          </ValuePointInner>
        </Fade>
      </ValuePoint>
    ))}
  </ValuePoints>
);
const HelpSection: React.SFC<HelpProps> = ({ valuePoints, title, link }) => (
  <Container>
    <Row padding={30} gutterWidth={26} verticalAlignment="stretch">
      <Column span={1} spanMedium={0} />
      <Column span={5} spanMedium={12}>
        <ValuePointsComponent valuePoints={valuePoints} />
      </Column>
      <Column span={1} spanMedium={0} />
      <Column span={4} spanMedium={12}>
        <TitleWrapper>
          <Fade bottom cascade>
            <Title>{title}</Title>
          </Fade>
          {link && (
            <Fade left cascade distance="10%">
              <LinkContainer>
                <Link to={link.to}>{link.text}</Link>
                <Arrow>
                  <img src={arrowIcon} alt="arrow icon" />
                </Arrow>
              </LinkContainer>
            </Fade>
          )}
        </TitleWrapper>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default HelpSection;
