import { StateHandler, StateHandlerMap } from "recompose";
import { RefObject } from "react";

export interface MarketingYield {
  marketingYieldRate: number;
}

export interface LegalYield {
  legalYieldRate: number;
  legalYieldDate: string;
}

export interface ContentfulProps {
  contentfulData: {
    metaTitle: string;
    marketoFormTopId: number;
    marketoFormTopPopUpFormId: number;
    marketoFormBottomId: number;
    marketoFormBottomPopUpFormId: number;
    heroTitle: string;
    orangeTextInsideTitle: string;
    refCode: string;
  };
}

export interface PageExternalProps {
  location: Location;
  pageContext: MarketingYield & LegalYield & ContentfulProps;
}

export interface PageStateProps {
  heroFormWrapperRef: RefObject<HTMLDivElement>;
  headerCutoff: number;
  shouldDisplayButton: boolean;
}

export type PageStateHandlerProps = StateHandlerMap<PageStateProps> & {
  setHeaderCutoff: StateHandler<PageStateProps>;
  setShouldDisplayButton: StateHandler<PageStateProps>;
};

export interface PageHandlerProps {
  calculateHeaderCutoff: () => void;
}

type PageInnerProps = PageStateProps & PageStateHandlerProps;

export type PageProps = PageExternalProps & PageInnerProps;

export enum TextType {
  PRIMARY = "primary",
  SECONDARY = "secondary",
}

export interface TitleText {
  type: TextType;
  text: string;
}

export interface IconProps {
  src?: string;
  iconHeight: number;
  iconWidth: number;
}

export interface ValuePointProps {
  iconProps: IconProps;
  title?: string;
  body: string;
}

export type ValuePointsList = Array<ValuePointProps>;

export interface ValuePointsProps {
  valuePoints: ValuePointsList;
}

interface ImgSrc {
  path: string;
  width: number;
}

interface VideoSrc {
  videoPrefix: string;
  mobileImage: string;
  videoPoster?: string;
  maxWidth?: number;
  boxShadow?: boolean;
}

export interface ImgSrcSet {
  defaultSrc: string;
  mobile?: ImgSrc;
  mobileRetina?: ImgSrc;
  desktop?: ImgSrc;
  maxWidth?: number;
  boxShadow?: boolean;
}

export interface HeroImageProps {
  desktopWidth: number;
  defaultSrc: string;
  mobile?: ImgSrc;
  desktop?: ImgSrc;
}

export interface HeroMessagesProps {
  valuePoints: ValuePointsList;
  setHeroFormWrapperRef?: RefObject<HTMLDivElement>;
}

export interface TitleProps {
  orangeText: string | RegExp;
  title: string;
}

export interface HeroContentfulProps {
  legalYieldDate: string;
  formId: number;
  topForm: number;
}

export type HeroProps = HeroMessagesProps & HeroContentfulProps;

export enum VisualType {
  IMAGE = "image",
  VIDEO = "video",
  ANIMATED_IMAGE = "animated_image",
  PULSE_IMAGE = "pulse_image",
}

export interface DetailProps {
  key: string;
  title: string | JSX.Element;
  body: string | JSX.Element;
  textColumns?: number;
  visualColumns?: number;
  visualType?: VisualType;
  imgSrc?: ImgSrcSet;
  videoSrc?: VideoSrc;
  visualFirstDesktop?: boolean;
}

export interface DetailVisualProps {
  visualColumns: number;
  visualType: VisualType;
  imgSrc?: ImgSrcSet;
  videoSrc?: VideoSrc;
}

export interface DetailImageProps {
  visualColumns: number;
  defaultSrc: string;
  mobile?: ImgSrc;
  mobileRetina?: ImgSrc;
  desktop?: ImgSrc;
  maxWidth?: number;
  boxShadow?: boolean;
}

export interface DetailsProps {
  details: Array<DetailProps>;
}

export interface IconProps {
  src?: string;
  iconHeight: number;
  iconWidth: number;
  placeholderBackground?: boolean;
}

export interface ValuePointProps {
  iconProps: IconProps;
  title?: string;
  body: string;
}

interface LogoProps {
  src: string;
  height: number;
  width: number;
}

export interface QuoteProps {
  quote: string | JSX.Element;
  speaker: string;
  logo: LogoProps;
}

interface LinkProps {
  text: string;
  to: string;
}

export interface HelpProps {
  valuePoints: ValuePointsList;
  title: string;
  link?: LinkProps;
  gutterBackground?: string;
}

export interface BottomProps {
  formId: number;
  bottomForm: number;
  setBottomFormWrapperRef?: RefObject<HTMLDivElement>;
}

export type LegalProps = LegalYield & { gutterBackground?: string };
