import React from "react";
import styled from "@emotion/styled";
import { padding } from "polished";

import { breakPoints } from "components/Grid";

import GeminiAlternatingDetail from "components/product/business-cash-account/GeminiAlternatingDetail";

import { DetailsProps } from "components/product/business-cash-account/types";

const Container = styled("div")({
  padding: 0,
  [breakPoints.medium]: {
    ...padding(72, 0),
  },
});

const GeminiAlternatingDetailList: React.SFC<DetailsProps> = ({ details }) => (
  <Container>
    {details.map((detailProps, index) => (
      <GeminiAlternatingDetail
        key={detailProps.key}
        visualFirstDesktop={index % 2 === 0}
        {...detailProps}
      />
    ))}
  </Container>
);

export default GeminiAlternatingDetailList;
